*, *::after, *::before{
    margin: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
}

body{
    font-size: 1.6rem;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    color: darkgray;
}

.nav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 8vh;
    /* background: rgb(0, 28, 73); */
}

.nav-brand{
    /* text-transform: uppercase; */
    font-weight: bold;
    margin-left: 3rem;
    font-family: cursive;
    font-style: italic;
    padding-right: 5rem;
    font-size: 2rem;
}

.nav-menu{
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 3rem;
    position: absolute;
    right: 10rem;
}

.nav-item{
    margin-left: 5rem;
    font-size: larger;
}

.navbar{
    margin-top: 3rem;
    padding-bottom: 5rem;
}

