body{
    background-color: aliceblue;
}

.heading h1{
    font-family: arial;
    font-size: 3.5rem;
    font-weight: 800;
}
.subheading{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    letter-spacing: 0.7rem;
}

.about-section{
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 35px;
}

#about-container{
    height: 80vh;
    box-shadow: 1px 1px 20px 1px grey;
    background-color: white;
    margin-top: 35px;
    padding: 0px 0px;
}

#about-container .row{
    width: 100%;
    height: 100%;
}

.about-img{
    background-image: url("../../assets/Home/profilepic.jpg");
    background-size: cover;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
}

#about-container img{
    height: 80vh;
}

.about-content{
    text-align: left;
    padding: 20px 45px;
    letter-spacing: 0.1rem;
}

.about-highlights{
    margin-top: 40px;
}

.about-ul li{
    list-style-type: square;
}

.about-btns{
    margin-top: 7rem;
    text-align: center;
}

#resume-btn:hover{
    background-color:orange
}

@media screen and (max-width:2024px){
    #about-container{
        height: auto;
    }
    #about-container img{
        height: 50vh;
    }
}

@media screen and (max-width:551px){
    #about-container img{
        height: 40vh;
    }
}