.contact-section{
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 35px;
    margin-top: 100px;
}

.contact-img{
    text-align: left;
    color: white;
    padding: 20px 0px;
}

.contact-img .heading, .subheading{
    margin: 10px 30px;
}

.left span{
    margin-left: 30px;
    height: 20px;
    color: grey;
}

.subheading i{
    margin: 15px 10px 0px 10px;
}


#contact-container{
    height: 80vh;
    box-shadow: 1px 1px 20px 1px grey;
    background-color:rgb(0, 10, 40);
    margin-top: 35px;
    margin-bottom: 50px;
    border-radius: 10px;
    letter-spacing: 0.6rem;
    padding-bottom: 8rem;
}

.contact-form{
    background-color: white;
    border-radius: 10px;
    margin: 15rem 10px 10px 10px;
    height: 50vh;
    letter-spacing: 0.3rem;
    padding: 30px 30px;
    text-align: left;
}

.form-control{
    border: none;
    border-radius: 5px;
    background-color: rgb(235, 235, 235);
    height: 4vh;
}

.left{
    margin-top: 10rem;
}

#contact-btn{
    height: 3.5rem;
    width: 13rem;
    margin-top: 3rem;
    padding: 8px 10px 10px;
}
#contact-btn span{
    padding-bottom: 10px;
}

.email-img{
    background-image: url("../../assets/Home/contact-email.jpg");
    background-size: cover;
    height: 25vh;
    padding: 0px 0px;
}

@media screen and (max-width:991px){
    #contact-container{
        height: auto;
    }
    .contact-img{
        height: 25rem;
    }
}